<template>
  <el-card>
    <el-row>
      <el-button type="primary" size="small" @click="createDataBase"> </el-button>
      <el-pagination background
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="search.page"
                     :page-size="search.pageSize"
                     :page-sizes="[10, 20, 50, 100]"
                     :total="search.total" layout="total, sizes, prev, pager, next" style="float: right">
      </el-pagination>
    </el-row>
    <el-row>
      <el-table :data="tableData" height="600">
        <el-table-column prop="dbName" label=""></el-table-column>
        <el-table-column prop="databaseType" label="">
          <template slot-scope="scope">
            <el-tag>{{getDBStatus(scope.row.databaseType)}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="url" label="URL"></el-table-column>
        <el-table-column prop="description" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" type="text" @click="EditDataBase(scope.row)"></el-button>
            <el-button size="small" type="text" @click="deleteDataBase(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-dialog :visible.sync="dialog.visible" :close-on-click-modal=false width="400px">
      <el-form label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="dialog.dataBaseInfo.dbName" style="width: 220px"/>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="dialog.dataBaseInfo.databaseType" placeholder="" style="width: 220px">
            <el-option v-for="item in dialog.dbType" :key="item.value" :label="item.label" :value="item.value"/>
          </el-select>
        </el-form-item>
        <el-form-item label="URL">
          <el-input v-model="dialog.dataBaseInfo.url" style="width: 220px"/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialog.dataBaseInfo.username" style="width: 220px"/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialog.dataBaseInfo.password" style="width: 220px"/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="dialog.dataBaseInfo.description" style="width: 220px"/>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDataBase"> </el-button>
        <el-button @click="dialog.visible = false"> </el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
    export default {
      data(){
        return {
          search:{
            page: 1,
            pageSize: 10,
            total: 0,
          },
          tableData: [],
          editType: null,
          dialog: {
            visible: false,
            dataBaseInfo:{},
            defaultDataBaseInfo: {
              variableName: '',
              variableValue: '',
              description: ''
            },
            dbType: [{
              label: 'MySQL',
              value: 1,
            },{
              label: 'Oracle',
              value: 2,
            }]
          },
        }
      },
      mounted(){
        this.getDataBaseList()
      },
      methods:{
        getDataBaseList(){
          this.$axios({
            method:"post",
            url: "/db/list",
            headers:{
              'Content-type': 'application/json'
            },
            data:this.search
          }).then(
            res=>{
              this.tableData = res.data.result.list;
              this.search.total = res.data.result.total;
            },err =>{
            });
        },
        createDataBase(){
          this.dialog.visible = true;
          this.editType = 1;
          this.dialog.dataBaseInfo = {...this.dialog.defaultDataBaseInfo};
        },
        handleSizeChange(val){
          this.search.pageSize = val
          this.getDataBaseList()
        },
        handleCurrentChange(val){
          this.search.page = val
          this.getDataBaseList()
        },
        getDBStatus(status){
          if (status === 1){
            return 'MySQL'
          } else if (status === 2){
            return 'Oracle'
          }
        },
        EditDataBase(row){
          this.dialog.visible = true;
          this.editType = 2;
          this.dialog.dataBaseInfo = {...row};
        },
        deleteDataBase(id){
          this.$axios({
            method:"post",
            url: "/db/delete",
            headers:{
              'Content-type': 'application/json'
            },
            data:id,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.getDataBaseList();
                this.$message.success(res.data.result);
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        },
        saveDataBase(){
          let url = "";
          if (this.editType === 1){
            url = "/db/create";
          } else if (this.editType === 2){
            url = "/db/update"
          }
          this.$axios({
            method:"post",
            url: url,
            headers:{
              'Content-type': 'application/json'
            },
            data:this.dialog.dataBaseInfo,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.dialog.visible = false;
                this.getDataBaseList();
                this.$message.success(res.data.result);
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        }
      }
    }
</script>

<style scoped>

</style>



