<template>
  <el-container style="height: 900px; border: 1px solid #eee">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <!---->
      <el-menu default-active="1" @select="handleSelect">
        <el-menu-item index="1">
          <i class="el-icon-zoom-in"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="2">
          <i class="el-icon-edit-outline"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="3">
          <i class="el-icon-coin"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
    </el-aside>
    <el-main>
      <!---->
      <component :is="currentView"></component>
    </el-main>
  </el-container>
</template>

<script>
  import comGlobalVarible from '@/components/GlobalVarible'
  import comCategory from '@/components/Category'
  import comDataBaseConfig from '@/components/DataBaseConfig'
  export default {
    components:{
      comGlobalVarible,
      comCategory,
      comDataBaseConfig,
    },
    data(){
      return{
        currentView: comGlobalVarible,

      }
    },
    methods:{
      handleSelect(key){
        if (key === '1'){
          this.currentView = 'comGlobalVarible';
        } else if (key === '2'){
          this.currentView =  'comCategory';
        } else if (key === '3'){
          this.currentView = 'comDataBaseConfig'
        }
      },
    }
  }
</script>

<style scoped>
</style>



