<template >
  <div>
    <!---->
    <el-row>
      <el-card>
        <el-row style="display: flex">
          <el-switch v-model="collectionInfo.isFocus" active-color="#13ce66" inactive-color="#dddddd" active-text="" @change="doFocus"></el-switch>
          <p style="margin: auto" >{{collectionInfo.collectionName}}</p>
          <el-button type="primary" size="small" icon="el-icon-setting" style="float: right" @click="collectionEditDialog.visible = true"/>
          <el-dialog title="" :visible.sync="collectionEditDialog.visible" :modal="false" width="500px">
            <el-form label-position="right">
              <el-form-item label="" >
                <el-input style="width: 220px" v-model="collectionInfo.collectionName"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-switch v-model="collectionInfo.wxPush" active-color="#13ce66"></el-switch>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button type="primary" @click="editCollection"> </el-button>
            </div>
          </el-dialog>
        </el-row>
      </el-card>
    </el-row>
    <div style="height: 5px"/>
    <el-row :gutter="5">
      <!---->
      <el-col :span="8">
        <el-card style="height: 140px">
          <el-form ref="form" :model="collectionInfo" label-width="80px">
            <el-form-item label="">
              <el-input-number v-model="collectionInfo.threadPoolSize" size="small" @change="changePoolSize" :min="1" :max="99"/>
            </el-form-item>
            <el-form-item label="">
              <el-input-number v-model="collectionInfo.repeatTimes" size="small" @change="changeRepeatTimes" :min="1" :max="9999"/>
            </el-form-item>
          </el-form>
        </el-card>
        <div style="height: 5px"/>
        <el-card style="height: 700px">
          <el-row>
            <el-button type="primary" size="small" @click="createVariable"> </el-button>
          </el-row>
          <el-row>
            <el-table :data="collectionInfo.variableList" height="600">
              <el-table-column prop="variableName" label="">
                <template slot-scope="scope">
                  <el-button type="text" v-model="scope.row.variableName" @click="editVariable(scope.row)">{{scope.row.variableName}}</el-button>
                </template>
              </el-table-column>
              <el-table-column prop="variableValue" label=""></el-table-column>
              <el-table-column prop="description" label=""></el-table-column>
            </el-table>
          </el-row>
        </el-card>
        <el-dialog :visible.sync="variableDialog.visible" :close-on-click-modal=false :modal="false" width="400px">
          <el-form label-position="right" label-width="80px">
            <el-form-item label="">
              <el-input v-model="variableDialog.variableInfo.variableName" style="width: 220px"/>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="variableDialog.variableInfo.variableValue" style="width: 220px"/>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="variableDialog.variableInfo.description" style="width: 220px"/>
            </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="variableOperation(1)" size="small"> </el-button>
            <el-button type="error" @click="variableOperation(2)" size="small" v-show="variableDialog.deleteButtonIsShow"> </el-button>
          </span>
        </el-dialog>
      </el-col>
      <!--case-->
      <el-col :span="16">
        <el-card style="height: 700px">
          <el-row>
            <el-button size="small" type="primary" @click="caseManageDialog.visible = true">Case</el-button>
            <el-button size="small" type="primary" @click="collectionExcute"> <i class="el-icon-caret-right"/></el-button>
            <div style="float: right">
              <span style="font-size: 14px;color:#606266;">{{collectionInfo.excuterName}}</span>
              <span style="font-size: 14px;color:#606266;">{{collectionInfo.excuteDatetime}}</span>
              <el-button size="small" type="primary" style="margin-left: 10px" @click="openReport"></el-button>
            </div>
          </el-row>
          <div class="dotted"/>
          <div style="margin-top: 10px;height: 600px">
            <vuescroll :ops="scroll">
              <draggable :list="caseInfo.caseList" v-bind="dragOptions" @end="orderSave">
                <div v-for="c in caseInfo.caseList" class="api-div">
                  <el-row style="margin: 11px 10px">
                    <el-col :span="21">
                      <span class="line-limit-length" style="width: 95%;font-size: 15px">{{c.caseName}}</span>
                    </el-col>
                    <el-col :span="2">
                      <el-tag :type="getTagType(c.caseType)" size="mini" style="transform: translateY(-3px)">{{getTypeName(c.caseType)}}</el-tag>
                    </el-col>
                    <el-col :span="1">
                      <div class="delete-button" @click="deleteCase(c)"></div>
                    </el-col>
                  </el-row>
                </div>
              </draggable>
            </vuescroll>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <!--Case-->
    <el-dialog :visible.sync="caseManageDialog.visible" :close-on-click-modal=false :modal="false" width="95%" top="20px">
      <el-row>
        <!--Case-Case-->
        <el-col :span="6">
          <el-card style="height: 600px">
            <div style="height: 580px;width: 100%">
              <vuescroll :ops="scroll">
                <draggable :list="caseInfo.caseList" group="caseList" v-bind="dragOptions" @change="orderSave" style="height: 580px">
                  <div v-for="c in caseInfo.caseList" class="api-div">
                    <el-row style="margin: 11px 10px">
                      <el-col :span="18">
                        <span class="line-limit-length" style="width: 95%;font-size: 15px">{{c.caseName}}</span>
                      </el-col>
                      <el-col :span="4">
                        <el-tag :type="getTagType(c.caseType)" size="mini">{{getTypeName(c.caseType)}}</el-tag>
                      </el-col>
                      <el-col :span="2">
                        <div class="delete-button" @click="deleteCase(c)"></div>
                      </el-col>
                    </el-row>
                  </div>
                </draggable>
              </vuescroll>
            </div>
          </el-card>
        </el-col>
        <!--case-case-->
        <el-col :span="18">
          <el-card style="margin-left: 6px;height: 600px">
            <!--case-case--->
            <div>
              <el-select v-model="caseManageDialog.caseType" size="small" style="width: 100px" @change="chaneCaseType">
                <el-option v-for="item in caseManageDialog.caseTypeList" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
              <el-input v-model="caseManageDialog.search.commomCase.caseName" placeholder="" size="small" style="width: 120px" @change="searchCaseList"></el-input>
              <el-select v-model="caseManageDialog.search.commomCase.createrCode" placeholder="" size="small" style="width: 120px" clearable filterable @change="searchCaseList">
                <el-option v-for="item in caseManageDialog.search.createrList" :key="item.createrCode" :label="item.createrName" :value="item.createrCode">
                  <span style="float: left">{{item.createrName}}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{item.createrCode}}</span>
                </el-option>
              </el-select>
              <el-pagination background
                             :current-page="caseManageDialog.search.page"
                             :page-size="caseManageDialog.search.pageSize"
                             :total="caseManageDialog.search.total"
                             @current-change="handleCurrentChange"
                             layout="total, prev,pager, next" style="float: right">
              </el-pagination>
            </div>
            <div class="dotted"/>
            <!--case-case--->
            <div style="margin-top: 10px">
              <el-row style="margin: 11px 10px">
                <el-col :span="2">
                  <p/>
                </el-col>
                <el-col :span="12">
                  <span>Case</span>
                </el-col>
                <el-col :span="10">
                  <span ></span>
                </el-col>
              </el-row>
              <draggable :list="caseManageDialog.AllCaseList" v-bind="dragOptions" :group="{name: 'caseList', pull: 'clone', put: false }">
                <div v-for="c in caseManageDialog.AllCaseList" class="api-div">
                  <el-row style="margin: 11px 10px">
                    <el-col :span="2">
                      <el-tag :type="getTagType(c.caseType)" size="mini">{{getTypeName(c.caseType)}}</el-tag>
                    </el-col>
                    <el-col :span="12">
                      <span class="line-limit-length" style="width: 95%;font-size: 15px">{{c.caseName}}</span>
                    </el-col>
                    <el-col :span="10">
                      <span class="line-limit-length" style="font-size: 14px;width: 95%">{{c.description}}</span>
                    </el-col>
                  </el-row>
                </div>
              </draggable>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
  import draggable from "vuedraggable";
  import vuescroll from 'vuescroll';
  export default {
    props:{
      collectionId: {
        type: Number
      }
    },
    data(){
      return{
        scroll:{
          vuescroll: {
            mode: 'native',
            sizeStrategy: 'percent',
          },
          scrollPanel: {
            easing: 'easeInQuad',
            speed: 800,
            // caseNamediv
            scrollingX: false,
          },
          rail: {
            // 
            opacity: '10',
            background: undefined,
            size: '12px'
          },
          bar: {
            background: '#bbbbbb',
            keepShow: false,
            size: '10px',
            minSize: 0.2
          }
        },
        collectionInfo: {
          isFocus: false,
          collectionName: null,
          variableList: [],
          excuterName: null,
          excuteDatetime: null,
          reportPath: null,
          wxPush: false,
          repeatTimes: 1,
          threadPoolSize: 1,
        },
        collectionEditDialog:{
          visible: false,
        },
        variableDialog: {
          visible: false,
          deleteButtonIsShow: false,
          variableInfo:{
            collectionId: null,
          },
          defaultVariableInfo: {
            variableName: '',
            variableValue: '',
            description: ''
          },
        },
        caseInfo:{
          collectionId: null,
          caseList: [],
        },
        caseManageDialog:{
          visible: false,
          caseType: 1,
          caseTypeList:[{
            label: 'Http',
            value: 1,
          },{
            label: 'Dubbo',
            value: 2,
          },{
            label: 'Sql',
            value: 3,
          }],
          search: {
            systemNameList: [],
            createrList: [],
            page: 1,
            pageSize: 10,
            total: 0,
            commomCase:{
              caseName: '',
              createrCode: '',
            },
          },
          AllCaseList: []
        }
      }
    },
    mounted(){
      this.getCollectionDetail();
      this.getCollectionCaseList();
      this.searchCaseList();
      this.getSystemNameList();
      this.getCreaterList();
    },
    methods:{
      // 
      editCollection(){
        this.$axios({
          method:"post",
          url: '/collection/edit',
          headers:{
            'Content-type': 'application/json'
          },
          data: this.collectionInfo,
        }).then(
          res=>{
            this.collectionEditDialog.visible = false;
            this.getCollectionDetail();
          },err =>{
          });
      },
      // 
      getCollectionDetail(){
        this.$axios({
          method:"post",
          url: '/collection/detail',
          headers:{
            'Content-type': 'application/json'
          },
          data: this.collectionId,
        }).then(
          res=>{
            this.collectionInfo = res.data.result;
          },err =>{
          });
      },
      // 
      getSystemNameList(){
        this.$axios({
          method:"get",
          url: "/category/list",
        }).then(
          res=>{
            this.caseManageDialog.search.systemNameList = res.data.result;
          },err =>{
          });
      },
      // 
      getCreaterList(){
        this.$axios({
          method:"get",
          url: "/http/creater",
        }).then(
          res=>{
            this.caseManageDialog.search.createrList = res.data.result;
          },err =>{
          });
      },
      // tagtype
      getTagType(caseType){
        if (caseType === 1 || caseType === 2){
          return 'primary'
        } else if (caseType === 'GET'){
          return 'success'
        } else if (caseType === 'PUT'){
          return 'warning'
        } else if (caseType === 3){
          return 'danger'
        }
      },
      getTypeName(caseType){
        if (caseType === 1){
          return 'Http'
        } else if (caseType === 2){
          return 'Dubbo'
        } else if (caseType === 3){
          return 'MySql'
        }
      },
      // 
      doFocus(val){
        this.$axios({
          method:"post",
          url: '/collection/focus',
          headers:{
            'Content-type': 'application/json'
          },
          data:{
            focusType: val,
            collectionId: this.collectionId
          }
        })
      },
      changePoolSize(val){
        this.collectionInfo.threadPoolSize = val
        this.editCollection()
      },
      changeRepeatTimes(val){
        this.collectionInfo.repeatTimes = val
        this.editCollection()
      },
      createVariable(){
        this.variableDialog.editType = 1;
        this.variableDialog.deleteButtonIsShow = false;
        this.variableDialog.visible = true;
        this.variableDialog.variableInfo = {...this.variableDialog.defaultVariableInfo};
        this.variableDialog.variableInfo.collectionId = this.collectionId
      },
      editVariable(row){
        this.variableDialog.editType = 2;
        this.variableDialog.deleteButtonIsShow = true;
        this.variableDialog.visible = true;
        this.variableDialog.variableInfo = {...row};
      },
      variableOperation(type){
        let requestUrl = "";
        if (type === 2){
          requestUrl = '/collection/variable/delete'
        } else if (type === 1) {
          if (this.variableDialog.editType === 1){
            requestUrl = '/collection/variable/insert'
          } else if (this.variableDialog.editType === 2){
            requestUrl = '/collection/variable/update'
          }
        }
        this.$axios({
          method:"post",
          url: requestUrl,
          headers:{
            'Content-type': 'application/json'
          },
          data:this.variableDialog.variableInfo
        }).then(
          res=>{
            this.$message.success(res.data.result);
            this.variableDialog.visible = false;
            this.getCollectionDetail();
          },err =>{
          });
      },
      // api list
      getCollectionCaseList(){
        this.$axios({
          method:"post",
          url: '/collection/case/list',
          headers:{
            'Content-type': 'application/json'
          },
          data:{
            collectionId: this.collectionId
          }
        }).then(
          res=>{
            this.caseInfo.caseList = res.data.result;
            this.caseInfo.collectionId = this.collectionId
          },err =>{
          });
      },
      // api
      orderSave(){
        // casecase
        let caseIds = {1:[],2:[],3:[]}
        for (let c of this.caseInfo.caseList){
          if (caseIds[c.caseType].indexOf(c.id) > -1) {
            this.$message.error("!")
            this.getCollectionCaseList()
            return
          }
          caseIds[c.caseType].push(c.id)
        }
        this.$axios({
          method:"post",
          url: '/collection/case/order',
          headers:{
            'Content-type': 'application/json'
          },
          data:this.caseInfo
        }).then(
          res=>{
            if (!res.data.isSuccess) {
              this.$message.error(res.data.msg);
              this.getCollectionCaseList();
            }
          },err =>{
            this.$message.error("");
          });
      },
      // case
      deleteCase(caseInfo){
        this.$axios({
          method:"post",
          url: '/collection/case/delete',
          headers:{
            'Content-type': 'application/json'
          },
          data:caseInfo
        }).then(
          res=>{
            if (!res.data.isSuccess) {
              this.$message.error(res.data.msg);
            }
            this.getCollectionCaseList();
          },err =>{
          });
      },
      handleCurrentChange(val) {
        this.caseManageDialog.search.page = val;
        this.searchCaseList();
      },
      // case
      chaneCaseType(){
        this.caseManageDialog.search.page = 1
        this.searchCaseList()
      },
      // case
      searchCaseList(){
        let url = ''
        let data = this.caseManageDialog.search
        let caseInfo = {...this.caseManageDialog.search.commomCase}
        if (this.caseManageDialog.caseType === 1){
          url = '/http/search'
          data['httpCase'] = caseInfo
        } else if (this.caseManageDialog.caseType === 2){
          url = '/dubbo/search'
          data['dubboCase'] = caseInfo
        } else if (this.caseManageDialog.caseType === 3){
          url = '/sql/search'
          data['sqlCase'] = caseInfo
        }
        this.$axios({
          method:"post",
          url: url,
          headers:{
            'Content-type': 'application/json'
          },
          data:data
        }).then(
          res=>{
            this.caseManageDialog.AllCaseList = res.data.result.list;
            // casecaseType
            for (let i = 0 ; i < this.caseManageDialog.AllCaseList.length; i++){
              this.caseManageDialog.AllCaseList[i]['caseType'] = this.caseManageDialog.caseType
            }
            this.caseManageDialog.search.total = res.data.result.total;
          },err =>{
          });
      },
      collectionExcute(){
        this.$axios({
          method:"post",
          url: '/collection/run',
          headers:{
            'Content-type': 'application/json'
          },
          data: this.collectionId
        }).then(
          res=>{
            if (res.data.isSuccess){
              this.$message.success(res.data.result);
              this.getCollectionDetail();
            }
          },err =>{
          });
      },
      openReport(){
        window.open(this.collectionInfo.reportPath, '_blank');
      },
    },
    components: {
      draggable,
      vuescroll,
    },
    computed: {
      dragOptions() {
        return {
          animation: 300,
        };
      },

    }
  }
</script>

<style scoped>
  .dotted {
    margin-top: 10px;
    width:100%;
    border-top:1px silver dashed;
  }
  .api-div {
    width: 98%;
    border-radius: 5px;
    border: 2px solid #F2F6FC;
    height: 40px;
  }
  .api-div:hover{
    background: #F2F6FC;
    box-shadow: 1px 3px 5px #DCDFE6;
  }
  .delete-button{
    margin-left: 15px;
    display:none ;
  }
  .api-div:hover .delete-button{
    margin-left: 15px;
    display:block ;
  }
  .line-limit-length {
    color: #606266;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



