<template>
  <div id="backImage">
    <div style="display: flex;justify-content: center;margin-top: 200px">
      <el-card style="width: 360px;opacity:0.8">
        <div slot="header">
          <span>{{title}}</span>
        </div>
        <el-form label="right" label-width="80px">
          <el-form-item label="">
            <el-input v-model="form.code" placeholder="" style="width: 200px"></el-input>
          </el-form-item>
          <el-form-item label="" v-show="regBtnShow">
            <el-input v-model="form.name" placeholder="" style="width: 200px"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="password" v-model="form.password" placeholder="" @keydown.enter.native="doLogin" style="width: 200px"></el-input>
          </el-form-item>
        </el-form>
        <el-row type="flex" align="middle" :gutter="20" v-show="loginBtnShow">
          <el-col>
            <el-button type="primary" style="float:right;" @click="doLogin"></el-button>
          </el-col>
          <el-col>
            <el-button type="primary" @click="goToRegistered"></el-button>
          </el-col>
        </el-row>
        <el-row type="flex" align="middle" :gutter="20" v-show="regBtnShow">
          <el-col>
            <el-button type="primary" style="float:right;" @click="registered"></el-button>
          </el-col>
          <el-col>
            <el-button @click="cancleRegistered"></el-button>
          </el-col>
        </el-row>
      </el-card>
    </div>
  </div>
</template>

<script>
  import {mapMutations} from 'vuex';
  export default {
    data() {
      return {
        title: '',
        form: {
          code: '',
          password: '',
        },
        loginBtnShow: true,
        regBtnShow: false,
      }
    },
    methods: {
      ...mapMutations(['changeLogin']),
      doLogin() {
        if (this.form.code === ''){
          this.$message.error('');
        }else if (this.form.password === ''){
          this.$message.error('');
        } else {
          let _this = this;
          this.$axios({
            method:"post",
            url: "/login",
            headers:{
              'Content-type': 'application/json'
            },
            data:this.form
          }).then(
            res=>{
              if (res.data.status === '501' || res.data.status === '500'){
                this.$message.error('');
              }else {
                this.$message({
                  message: '',
                  type: 'success'
                });
                _this.userToken = 'Bearer ' + res.data.body.accessToken;
                _this.changeLogin({ Authorization: _this.userToken });
                _this.$router.push({ path:'/index'});
                this.GLOBAL.userCode = res.data.body.name;
              }
            },err =>{
              console.log("");
            });
        }
      },
      goToRegistered(){
        this.title = ''
        this.loginBtnShow = false
        this.regBtnShow = true
      },
      cancleRegistered(){
        this.title = ''
        this.loginBtnShow = true
        this.regBtnShow = false
      },
      registered() {
        this.$axios({
          method:"post",
          url: "/registered",
          headers:{
            'Content-type': 'application/json'
          },
          data:this.form
        }).then(
          res=>{
            if (res.data.isSuccess === true){
              this.$message.success("");
              this.doLogin()
            } else if (res.data.isSuccess === false){
              this.$message.error(res.data.msg)
            }
          },err =>{
            console.log("");
          });

      },
    }
  }
</script>

<style scoped>
  #backImage{
    background: url("../assets/backImage.jpeg");
    background-size: 100% 100%;
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
  }

</style>



