<template>
  <el-card>
    <el-row>
      <el-button type="primary" size="small" @click="createVariable"> </el-button>
      <el-pagination background
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="table.page"
                     :page-size="table.pageSize"
                     :page-sizes="[10, 20, 50, 100]"
                     :total="table.total" layout="total, sizes, prev, pager, next" style="float: right">
      </el-pagination>
    </el-row>
    <el-row>
      <el-table :data="table.tableData" height="600">
        <el-table-column prop="variableName" label=""></el-table-column>
        <el-table-column prop="variableValue" label=""></el-table-column>
        <el-table-column prop="description" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" type="text" @click="EditVariable(scope.row)"></el-button>
            <el-button size="small" type="text" @click="deleteVariable(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-row>
    <el-dialog :visible.sync="variableDialog.visible" :close-on-click-modal=false width="400px">
      <el-form label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="variableDialog.variableInfo.variableName" style="width: 220px"/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="variableDialog.variableInfo.variableValue" style="width: 220px"/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="variableDialog.variableInfo.description" style="width: 220px"/>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveVariable"> </el-button>
        <el-button @click="variableDialog.visible = false"> </el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
    export default {
      data(){
        return {
          editType: 0, // 1. 2.
          table: {
            page: 1,
            pageSize: 10,
            total: 0,
            tableData:[]
          },
          variableDialog: {
            visible: false,
            variableInfo:{},
            defaultVariableInfo: {
              variableName: '',
              variableValue: '',
              description: ''
            },
          },
        }
      },
      mounted(){
        this.searchVariableList();
      },
      methods:{
        searchVariableList(){
          this.$axios({
            method:"post",
            url: "/variable/list",
            headers:{
              'Content-type': 'application/json'
            },
            data:{
              "page": this.table.page,
              "pageSize":this.table.pageSize,
            }
          }).then(
            res=>{
              this.table.tableData = res.data.result.list;
              this.table.total = res.data.result.total;
            },err =>{
            });
        },
        handleSizeChange(val) {
          this.table.pageSize = val;
          this.searchVariableList();
        },
        handleCurrentChange(val) {
          this.table.page = val;
          this.searchVariableList();
        },
        createVariable(){
          this.editType = 1;
          this.variableDialog.visible = true;
          this.variableDialog.variableInfo = {...this.variableDialog.defaultVariableInfo}

        },
        EditVariable(row){
          this.variableDialog.visible = true;
          this.editType = 2;
          this.variableDialog.variableInfo = {...row};
        },
        deleteVariable(id){
          this.$axios({
            method:"post",
            url: "/variable/delete",
            headers:{
              'Content-type': 'application/json'
            },
            data:id,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.searchVariableList();
                this.$message.success(res.data.result);
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        },
        saveVariable(){
          let url = "";
          if (this.editType === 1){
            url = "/variable/create";
          } else if (this.editType === 2){
            url = "/variable/update"
          }
          this.$axios({
            method:"post",
            url: url,
            headers:{
              'Content-type': 'application/json'
            },
            data:this.variableDialog.variableInfo,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.variableDialog.visible = false;
                this.searchVariableList();
                this.$message.success(res.data.result);
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        },
      }
    }
</script>

<style scoped>

</style>



