<template>
  <el-card>
    <el-button type="primary" size="small" @click="createCategory"> </el-button>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="categoryName" width="200" label=""/>
      <el-table-column prop="description" label=""/>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="small" type="text" @click="editCategory(scope.row)"></el-button>
          <el-button size="small" type="text" @click="deleteCategory(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog :visible.sync="editDialog.visible" :close-on-click-modal=false width="400px">
      <el-form label-position="right" label-width="80px">
        <el-form-item label="">
          <el-input v-model="editDialog.categoryInfo.categoryName" style="width: 220px"/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editDialog.categoryInfo.description" style="width: 220px"/>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveCategory"> </el-button>
        <el-button @click="editDialog.visible = false"> </el-button>
      </span>
    </el-dialog>
  </el-card>
</template>

<script>
    export default {
      data() {
        return {
          editType: 0, // 1. 2.
          tableData: [],
          editDialog: {
            visible: false,
            categoryInfo:{},
            defaultCategoryInfo: {
              categoryName: '',
              description: ''
            },
          },
        }
      },
      mounted(){
        this.getCategoryList();
      },
      methods: {
        getCategoryList(){
          this.$axios({
            method:"get",
            url: "/category/list",
          }).then(
            res=>{
              this.tableData = res.data.result;
            },err =>{

            });
        },
        createCategory(){
          this.editType = 1;
          this.editDialog.visible = true;
          this.editDialog.categoryInfo = {...this.editDialog.defaultCategoryInfo}
        },
        saveCategory(){
          let url = "";
          if (this.editType === 1){
            url = "/category/create";
          } else if (this.editType === 2){
            url = "/category/update"
          }
          this.$axios({
            method:"post",
            url: url,
            headers:{
              'Content-type': 'application/json'
            },
            data:this.editDialog.categoryInfo,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.editDialog.visible = false;
                this.getCategoryList();
                this.$message.success(res.data.result);
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        },
        editCategory(row){
          this.editType = 2;
          this.editDialog.visible = true;
          this.editDialog.categoryInfo = {...row}
        },
        deleteCategory(id){
          this.$axios({
            method:"post",
            url: "/category/delete",
            headers:{
              'Content-type': 'application/json'
            },
            data:id,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.getCategoryList();
                this.$message.success(res.data.result);
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
            },err =>{
            });
        }
      }
    }
</script>

<style scoped>

</style>



