<template>
  <div>
    <!---->
    <el-menu :default-active="activeIndex" mode="horizontal"
             background-color="#1E8AF9"
             text-color="#fff"
             active-text-color="#ffd04b"
             @select="handleSelect">
      <el-submenu index="1">
        <template slot="title">Case</template>
        <el-menu-item index="1-1">Http</el-menu-item>
        <el-menu-item index="1-2">Dubbo</el-menu-item>
        <el-menu-item index="1-3">SQL</el-menu-item>
      </el-submenu>
      <el-menu-item index="2"></el-menu-item>
      <el-menu-item index="3"></el-menu-item>
      <el-menu-item index="4"></el-menu-item>

      <a class="name">{{userName}}</a>
    </el-menu>

    <!---->
    <component :is="currentView"></component>

  </div>
</template>

<script>
  import comHttpCaseManage from '@/components/HttpCaseManage'
  import comDubboCaseManage from '@/components/DubboCaseManage'
  import comSqlCaseManage from '@/components/SqlCaseManage'
  import comCollectionList from  '@/components/CollectionList'
  import comConfig from '@/components/ConfigSideBar'
  import comFunctionAssistant from '@/components/FunctionAssistant'
  export default {
    data() {
      return {
        currentView: comHttpCaseManage,
        activeIndex: '1-1',
        userName: "000000",
      }
    },
    components:{
      comHttpCaseManage,
      comDubboCaseManage,
      comSqlCaseManage,
      comCollectionList,
      comConfig,
      comFunctionAssistant,
    },
    mounted() {
      this.getUserName()
    },
    methods: {
      handleSelect(key, keyPath) {
        let comKeyPath = keyPath[keyPath.length-1];
        if (comKeyPath === '1-1'){
          this.currentView = comHttpCaseManage;
        } else if (comKeyPath === '1-2'){
          this.currentView = comDubboCaseManage;
        } else if (comKeyPath === '1-3'){
          this.currentView = comSqlCaseManage;
        } else if (comKeyPath === '2'){
          this.currentView = comCollectionList;
        } else if (comKeyPath === '3'){
          this.currentView = comConfig;
        } else if (comKeyPath === '4'){
          this.currentView = comFunctionAssistant;
        } else if (comKeyPath === '4'){
          this.currentView = comHttpCaseManage;
        }
      },
      getUserName(){
        this.$axios({
          method:"get",
          url: "/username",
        }).then(
          res=>{
            this.userName = res.data.result.userName;
          },err =>{

          });
      },
    }
  };
</script>

<style scoped>
  .name{
    margin-top: 20px;
    margin-right: 60px;
    color:white;
    float: right;
  }
</style>



