<template >
  <div>
    <br/>
    <div>
      
      <el-select v-model="value" placeholder="" @change="getContent">
        <el-option
          v-for="item in data"
          :key="item.name"
          :label="item.name"
          :value="item.name">
        </el-option>
      </el-select>
    </div>
    <br/>
    <el-input type="textarea" :rows="10" v-model="content"style="width: 600px"></el-input>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        value: '',
        data:[{
          name: '__Random',
          content:'${__Random(param1,param2,param3)}\n' +
            'param1param2,param3()\n' +
            ': ${__Random(1,100,Number)}1~100Number${__Random(1,100)}1~100'
        },{
          name: '__time',
          content: '${__time(param1,param2)}\n' +
            'param1 param2()\n' +
            '\n' +
            '${__time(,)}   \n'+
            '${__time(/1000,TIME)}  TIME \n' +
            '${__time(yyyy-MM-dd,)}  yyyy-MM-dd \n' +
            '${__time(yyyy-MM-dd HH:mm:ss,DATE)}   yyyy-MM-dd HH:mm:ssDATE'
        }],
        content:'',
      }
    },
    methods: {
      getContent(){
        this.data.forEach((item)=>{
          if (item.name === this.value){
            this.content = item.content
          }
        })
      }
    }

  }
</script>

<style scoped>
</style>



