<template>
  <div>
    <el-card>
      <el-input v-model="search.sqlCase.caseName" placeholder="Case" style="width: 150px"></el-input>
      <el-select v-model="search.sqlCase.dataBaseId" placeholder="" style="width: 150px" clearable filterable>
        <el-option v-for="item in dataBaseList" :key="item.dbName" :label="item.dbName" :value="item.id"/>
      </el-select>
      <el-select v-model="search.sqlCase.createrCode" placeholder="" style="width: 150px" clearable filterable>
        <el-option v-for="item in createrList" :key="item.createrCode" :label="item.createrName" :value="item.createrCode">
          <span style="float: left">{{ item.createrName }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{ item.createrCode }}</span>
        </el-option>
      </el-select>
      <el-button  type="primary" style="float: right" @click="searchCaseList"> </el-button>
    </el-card>
    <br/>
    <el-card>
      <el-row>
        <el-button type="primary" size="small" @click="createCase"> </el-button>
        <el-pagination background
                       @size-change="handleSizeChange"
                       @current-change="handleCurrentChange"
                       :current-page="search.page"
                       :page-size="search.pageSize"
                       :page-sizes="[10, 20, 50, 100]"
                       :total="search.total" layout="total, sizes, prev, pager, next" style="float: right">
        </el-pagination>
      </el-row>
      <el-table :data="tableData" stripe>
        <el-table-column prop="caseName" label="Case" >
          <template slot-scope="scope">
            <el-button type="text" v-model="scope.row.caseName" @click="editCase(scope.row)">{{scope.row.caseName}}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="databaseName" label="" ></el-table-column>
        <el-table-column prop="createrName" label=""/>
        <el-table-column prop="sql" label="SQL" :show-overflow-tooltip=true />
        <el-table-column prop="description" label=""/>
        <el-table-column label="">
          <template slot-scope="scope">

              <el-button type="text" slot="reference" @click="deleteCase(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-dialog :visible.sync="dialog.dialogVisible" :close-on-click-modal=false width="1000px">
      <el-row v-show="dialog.userInfoIsShow">
        <el-col :span="3">
          <span>: {{dialog.caseInfo.createrName}}</span>
        </el-col>
      </el-row>
      <br/>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <el-form label-position="right" label-width="100px">
          <el-form-item label="">
            <el-select v-model="dialog.caseInfo.databaseId"  style="width: 500px">
              <el-option v-for="item in dataBaseList" :key="item.id" :label="item.dbName" :value="item.id"/>
            </el-select>
          </el-form-item>
          <el-form-item label="Case">
            <el-input  placeholder="Case" v-model="dialog.caseInfo.caseName" style="width: 500px"/>
          </el-form-item>
          <el-form-item label="SQL">
            <el-input type="textarea" :autosize="{ minRows: 3, maxRows: 6}" placeholder="SQL" v-model="dialog.caseInfo.sql" style="width: 500px"/>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" :autosize="{ minRows: 1, maxRows: 4}" placeholder="" v-model="dialog.caseInfo.description" style="width: 500px"/>
          </el-form-item>
        </el-form>
      </el-card>
      <br/>
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <el-collapse v-model="dialog.responseParams.activeCollapse">
          <el-collapse-item title="" name="variable">
            <el-button type="primary" size="mini" @click="addVarialbe"> </el-button>
            <el-form>
              <el-form-item v-for="(item,index) in dialog.caseInfo.variableList" :key="index" style="margin-bottom: 0px">
                <el-select v-model="item.extractMethod" placeholder="" size="small" style="width: 150px">
                  <el-option v-for="variableType in dialog.responseParams.variableTypeList" :key="variableType.value" :label="variableType.label" :value="variableType.value"/>
                </el-select>
                <el-input style="width: 250px" size="small" v-model="item.extractRule" placeholder=""></el-input>
                <el-tooltip class="item" effect="dark" content="VARIABLE_TYPE,${VARIABLE_TYPE}" placement="top-end">
                  <el-input style="width: 100px" size="small" v-model="item.variableName" placeholder=""></el-input>
                </el-tooltip>
                <el-button type="text" @click="deleteVarialbe(index)"><i class="el-icon-delete"></i> </el-button>
              </el-form-item>
            </el-form>
          </el-collapse-item>
          <el-collapse-item title="" name="debug" v-show="dialog.responseParams.canDebug">
            <el-button :type="dialog.responseParams.excuteButtonStauts" size="small" @click="excuteSql" :loading="dialog.responseParams.loading"> <i class="el-icon-caret-right el-icon--right"></i></el-button>
            <br/>
            <br/>
            <el-input v-model="dialog.caseInfo.result" type="textarea" :autosize="{ minRows: 6, maxRows: 10}" placeholder=""/>
          </el-collapse-item>
        </el-collapse>
      </el-card>
      <br/>
      <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="saveCase"> </el-button>
          <el-button @click="dialog.dialogVisible = false"> </el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        editType: 0, // 1. 2. 3.
        search: {
          page: 1,
          pageSize: 10,
          total: 0,
          sqlCase:{
            caseName: '',
            dataBaseId: '',
            createrCode: '',
          }
        },
        dataBaseList: [],
        createrList: [],
        tableData: [],
        dialog: {
          dialogVisible: false,
          userInfoIsShow: false,
          caseInfo: {},
          defaultCaseInfo: {
            createrName: "",
            createrCode: '',
            createDate: "",
            sql: "",
            description: "",
            variableList: [],
            result: '',
          },
          responseParams: {
            activeCollapse: 'debug',
            variable: [],
            variableTypeList: [{
              label: 'JsonPath',
              value: 1,
            },{
              label: '',
              value: 2,
            }],
            canDebug: false,
            loading: false,
            excuteButtonStauts: 'success',
          },
        }
      }
    },
    mounted(){
      this.getDataBaseList();
      this.getCreaterList();
      this.searchCaseList();
    },
    watch: {
      editType: function (val) {
        // ()
        if (val === 1){
          this.dialog.userInfoIsShow = false;
          this.dialog.responseParams.canDebug = false;
        } else if (val === 2){
          this.dialog.userInfoIsShow = true;
          this.dialog.responseParams.canDebug = true;
        }
      }
    },
    methods:{
      // 
      getDataBaseList(){
        this.$axios({
          method:"post",
          url: "/db/list",
          headers:{
            'Content-type': 'application/json'
          },
          data:{
            page: 1,
            pageSize: 100,
          },
        }).then(
          res=>{
            this.dataBaseList = res.data.result.list;
          },err =>{
          });
      },
      // 
      getCreaterList(){
        this.$axios({
          method:"get",
          url: "/sql/creater",
        }).then(
          res=>{
            this.createrList = res.data.result;
          },err =>{
          });
      },
      searchCaseList(){
        this.$axios({
          method:"post",
          url: "/sql/search",
          headers:{
            'Content-type': 'application/json'
          },
          data: this.search
        }).then(
          res=>{
            this.tableData = res.data.result.list;
            this.search.total = res.data.result.total;
          },err =>{
          });
      },
      createCase(){
        this.editType = 1;
        this.dialog.dialogVisible = true;
        this.dialog.caseInfo = {...this.dialog.defaultCaseInfo};
      },
      deleteCase(id){
        this.$axios({
          method:"post",
          url: "/sql/delete",
          headers:{
            'Content-type': 'application/json'
          },
          data:id,
        }).then(
          res=>{
            if (res.data.isSuccess===true){
              this.$message.success(res.data.result)
              this.searchCaseList();
            } else if(res.data.isSuccess===false){
              this.$message.error(res.data.msg);
            }
            resolve(res);
          },err =>{
          });
      },
      handleSizeChange(val){
        this.search.pageSize = val;
        this.searchCaseList();
      },
      handleCurrentChange(val){
        this.search.page = val;
        this.searchCaseList();
      },
      editCase(row){
        // 2
        this.editType = 2;
        this.dialog.dialogVisible = true;
        // case
        this.dialog.caseInfo = {...row};
      },
      addVarialbe(){
        this.dialog.caseInfo.variableList.push({extractMethod:'',extractRule:'',variableName:''});
      },
      deleteVarialbe(index){
        this.dialog.caseInfo.variableList.splice(index,1);
      },
      excuteSql(){
        let promise = new Promise((resolve,reject)=>{
          // 
          this.$axios({
            method:"post",
            url: "/sql/update",
            headers:{
              'Content-type': 'application/json'
            },
            data:this.dialog.caseInfo,
          }).then(
            res=>{
              if (res.data.isSuccess===true){
                this.searchCaseList();
              } else if(res.data.isSuccess===false){
                this.$message.error(res.data.msg);
              }
              resolve(res);
            },err =>{
            });
        });
        promise.then((res)=>{
          this.dialog.responseParams.loading = true;
          this.$axios({
            method:"post",
            url: "/sql/excute",
            headers:{
              'Content-type': 'application/json'
            },
            data:this.dialog.caseInfo,
          }).then(
            res=>{
              this.dialog.responseParams.loading = false;
              this.dialog.caseInfo = res.data.result;
              // 
            },err =>{
              this.dialog.responseParams.loading = false;
            });
        });
      },
      saveCase(){
        let requestUrl = '';
        if (this.editType === 1 || this.editType === 3) {
          requestUrl = "/sql/create";
        } else if (this.editType === 2){
          requestUrl = "/sql/update";
        }
        this.$axios({
          method:"post",
          url: requestUrl,
          headers:{
            'Content-type': 'application/json'
          },
          data:this.dialog.caseInfo,
        }).then(
          res=>{
            if (res.data.isSuccess===true){
              this.$message.success(res.data.result);
              this.dialog.dialogVisible = false;
              this.searchCaseList();
            } else if(res.data.isSuccess===false){
              this.$message.error(res.data.msg);
            }
          },err =>{
          });
      },
    }
  }
</script>

<style scoped>

</style>



