<template>
  <div>
    <el-tabs type="border-card" @tab-click="tabClick">
      <el-tab-pane label="">
        <el-row v-for="r in row" :key ="r" :gutter="10" class="row">
          <el-col v-for="c in getCol(r)" :key="c.id" :span="6" class="col">
            <div class="collection-div" @click="getDetail(c.id)">
              <el-row style="height: 30px">
                <p class="name-font">{{c.collectionName}}</p>
              </el-row>
              <el-row>
                <p class="date-font">{{c.excuteDatetime}}</p>
              </el-row>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-card>
          <el-row>
            <el-button type="primary" @click="createDialog.visible = true"> </el-button>
            <el-input v-model="search.collectionDetail.collectionName" @change="searchAllCollection"  placeholder="" style="width: 150px"></el-input>
            <el-select v-model="search.collectionDetail.createrCode" @change="searchAllCollection" placeholder="" style="width: 150px" clearable filterable>
              <el-option v-for="item in search.collectionDetail.createrList" :key="item.createrCode" :label="item.createrName" :value="item.createrCode">
                <span style="float: left">{{item.createrName}}</span>
                <span style="float: right; color: #8492a6; font-size: 13px">{{item.createrCode}}</span>
              </el-option>
            </el-select>
            <el-pagination background
                           @current-change="handleCurrentChange"
                           :current-page="search.page"
                           :page-size="search.pageSize"
                           :total="search.total" layout="total, prev, pager, next" style="float: right">
            </el-pagination>
          </el-row>
          <el-dialog :visible.sync="createDialog.visible" width="400px">
            <el-form label-position="right">
              <el-form-item label="">
                <el-input v-model="createDialog.collectionName" style="width: 220px"/>
              </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button type="primary" @click="createCollection"> </el-button>
              <el-button @click="createDialog.visible = false"> </el-button>
            </span>
          </el-dialog>
        </el-card>
        <br/>
        <el-row v-for="r in row" :key ="r" :gutter="10" class="row">
          <el-col v-for="c in getCol(r)" :key="c.id" :span="6" class="col">
            <div class="collection-div" @click="getDetail(c.id)">
              <el-row style="height: 30px">
                <p class="name-font">{{c.collectionName}}</p>
              </el-row>
              <el-row>
                <p class="date-font">{{c.excuteDatetime}}</p>
              </el-row>
            </div>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-date-picker
                v-model="records.collectionDetail.dateTime"
                type="datetimerange"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                value-format="yyyy-MM-dd HH:mm:ss"
                @change="chooseDate">
        </el-date-picker>
        <el-pagination background
                       @size-change="handleRecordsSizeChange"
                       @current-change="handleRecordsCurrentChange"
                       :current-page="records.page"
                       :page-size="records.pageSize"
                       :page-sizes="[10, 20, 50, 100]"
                       :total="records.total" layout="total, sizes, prev, pager, next" style="float: right">
        </el-pagination>
        <el-table ref="multipleTable" :data="recordsData" tooltip-effect="dark">
          <el-table-column prop="collectionName" label="" align="center" sortable/>
          <el-table-column prop="excuterName" label="" align="center" sortable/>
          <el-table-column prop="excuteDatetime" label="" align="center" sortable/>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <el-drawer :visible.sync="drawer" :with-header="false" :destroy-on-close="true" size="80%">
      <component :collectionId="collectionId" :is="currentView"></component>
    </el-drawer>
  </div>
</template>

<script>
  import comCollection from '@/components/CollectionDetail'
  export default {
    comments:{
      comCollection,
    },
    data () {
      return {
        createDialog: {
          visible: false,
          collectionName: '',
        },
        search: {
          page: 1,
          pageSize: 16,
          total: 0,
          collectionDetail:{
            collectionName: '',
            createrCode: '',
            createrList: [],
          }
        },
        row: 0,
        col: 0,
        collectionList: [],
        drawer:false,
        collectionId: null,
        currentView: '',
        records:{
          collectionDetail:{
            dateTime: [],
            startDate: '',
            endDate: '',
          },

          page: 1,
          pageSize: 10,
          total: 0,
        },
        recordsData:[]
      }
    },
    mounted(){
      this.getCreaterList();
      this.searchFocusCollection();
      this.getExcuteRecords()
    },
    methods: {
      tabClick(tab){
        if (tab.index === '0'){
          this.searchFocusCollection()
        } else if (tab.index === '1'){
          this.searchAllCollection()
        }
      },
      // 
      getCreaterList(){
        this.$axios({
          method:"get",
          url: "/collection/creater",
        }).then(
                res=>{
                  this.search.collectionDetail.createrList = res.data.result;
                },err =>{
                });
      },
      // 
      searchFocusCollection(){
        this.$axios({
          method:"get",
          url: "/collection/focuslist",
        }).then(
                res=>{
                  this.collectionList = res.data.result;
                  this.row = Math.ceil(this.collectionList.length / 4);
                },err =>{
                });
      },
      // 
      searchAllCollection(){
        this.$axios({
          method:"post",
          url: "/collection/list",
          headers:{
            'Content-type': 'application/json'
          },
          data: this.search
        }).then(
                res=>{
                  this.collectionList = res.data.result.list;
                  this.search.total = res.data.result.total;
                  this.row = Math.ceil(res.data.result.total / 4);
                },err =>{
                });
      },
      getCol(r){
        let total = this.collectionList.length;
        let i = (r-1)*4;
        if (4 * r <= total) {
          return this.collectionList.slice(i,i+4)
        }
        else {
          return this.collectionList.slice(i,total)
        }

      },
      createCollection(){
        this.$axios({
          method:"post",
          url: "/collection/create",
          headers:{
            'Content-type': 'application/json'
          },
          data:this.createDialog
        }).then(
                res=>{
                  if (res.data.isSuccess === true){
                    this.$message.success(res.data.result);
                    this.createDialog.visible = false;
                    this.searchAllCollection();
                  }
                },err =>{
                });
      },
      handleCurrentChange(val) {
        this.search.page = val;
        this.searchAllCollection();
      },
      getDetail(id){
        this.collectionId = id
        this.drawer = true
        this.currentView = comCollection
      },
      chooseDate(date){
        let promise = new Promise((resolve,reject)=>{
          if (date === null){
            this.records.collectionDetail.startDate = '1970-01-01 00:00:00';
            this.records.collectionDetail.endDate = '2099-12-31 23:59:59';
          } else {
            this.records.collectionDetail.startDate = date[0];
            this.records.collectionDetail.endDate = date[1];
          }
          resolve();
        });
        promise.then(
                this.getExcuteRecords()
        );
      },
      handleRecordsSizeChange(val){
        this.records.pageSize = val
        this.getExcuteRecords()
      },
      handleRecordsCurrentChange(val){
        this.records.page = val
        this.getExcuteRecords()
      },
      getExcuteRecords(){
        this.$axios({
          method:"post",
          url: "/collection/records",
          headers:{
            'Content-type': 'application/json'
          },
          data:this.records
        }).then(
                res=>{
                  if (res.data.isSuccess === true){
                    this.recordsData = res.data.result.list
                    this.records.total = res.data.result.total
                  }
                },err =>{
                });
      },

    }
  }
</script>

<style>
  .row {
    margin-bottom: 10px;
  }
  .col {
    border-radius: 4px;
  }
  .collection-div {
    background: white;
    border-radius: 5px;
    border: 2px solid #F2F6FC;
    height: 120px;
    transition: box-shadow 0.3s;
  }
  .collection-div:hover{
    background: #F2F6FC;
    transform: translateY(-5px);
    box-shadow: 1px 3px 5px #DCDFE6;
  }
  .name-font {
    color: #333333;
    font-size:20px;
    text-align:center;
  }
  .date-font {
    color: #666666;
    margin-top: 20px;
    font-size:14px;
    text-align:center;
  }
</style>



